import React from 'react';
import {
	Button,
	Grid,
	MenuItem,
	Select,
	Stack,
	TextField,
} from '@material-ui/core';
import { useFormikContext } from 'formik';
import _get from 'lodash.get';
import AnimateButton from '../../../../ui-component/extended/AnimateButton';
import { gridSpacing } from '../../../../store/constant';
import { TEditUserProfile } from '../../../../types';
import config from '../../../../config';
import { PhoneInput } from '../../components/PhoneInput';
import InputLabel from '../../../../ui-component/extended/Form/InputLabel';
import SubCard from '../../../../ui-component/cards/SubCard';

type Props = {
	isGroupDisable: boolean;
	groups: Array<object>;
	isAddNew?: boolean;
	isDisableEmail?: boolean;
};

const Profile = (props: Props) => {
	const { isGroupDisable, groups, isAddNew, isDisableEmail = true } = props;
	const {
		values,
		handleChange,
		handleBlur,
		touched,
		errors,
		setFieldValue,
		isSubmitting,
	} = useFormikContext<TEditUserProfile>();

	const title = isAddNew ? 'Add New User' : 'Edit Account Details';
	const buttonLabel = isAddNew ? 'Save' : 'Update';
	return (
		<Grid container spacing={gridSpacing}>
			<Grid item sm={6} md={8}>
				<SubCard title={title}>
					<Grid item xs={12} lg={6} sx={{ mb: 2 }}>
						<InputLabel>First Name</InputLabel>
						<TextField
							value={values.firstName}
							fullWidth
							placeholder='First Name'
							onChange={handleChange}
							onBlur={handleBlur}
							error={touched.firstName && Boolean(errors.firstName)}
							helperText={touched.firstName && errors.firstName}
							name='firstName'
							variant='outlined'
						/>
					</Grid>
					<Grid item xs={12} lg={6} sx={{ mb: 2 }}>
						<InputLabel>Last Name</InputLabel>
						<TextField
							id='lastName'
							value={values.lastName}
							fullWidth
							placeholder='Last Name'
							onChange={handleChange}
							onBlur={handleBlur}
							error={touched.lastName && Boolean(errors.lastName)}
							helperText={touched.lastName && errors.lastName}
							name='lastName'
						/>
					</Grid>
					<Grid item xs={12} lg={6} sx={{ mb: 2 }}>
						<InputLabel>Email</InputLabel>
						<TextField
							id='email'
							value={values.email}
							fullWidth
							placeholder='Email'
							onChange={handleChange}
							onBlur={handleBlur}
							error={touched.email && Boolean(errors.email)}
							helperText={touched.email && errors.email}
							name='email'
							disabled={isDisableEmail}
						/>
					</Grid>
					<Grid item xs={12} lg={6} sx={{ mb: 2 }}>
						<InputLabel>Phone</InputLabel>
						<PhoneInput
							country={config.locate.countryCode}
							placeholder=''
							value={values.phone}
							onChange={(value: string) => setFieldValue('phone', value)}
							onBlur={handleBlur}
							name='phone'
							error={touched.phone && errors.phone}
						/>
					</Grid>
					<Grid item xs={12} lg={6} sx={{ mb: 2 }}>
						<InputLabel>User Group</InputLabel>
						<Select
							value={values.userGroup}
							labelId='userGroup'
							id='userGroup'
							onChange={handleChange}
							disabled={isGroupDisable}
							onBlur={handleBlur}
							name='userGroup'
							displayEmpty
							error={touched.userGroup && Boolean(errors.userGroup)}
						>
							<MenuItem value='' disabled>
								<em>None</em>
							</MenuItem>
							{groups.map(group => {
								const value = _get(group, 'GroupName');
								const label = _get(group, 'Description');
								return (
									<MenuItem key={value} value={value}>
										{label}
									</MenuItem>
								);
							})}
						</Select>
					</Grid>
					<Grid item xs={12}>
						<Stack direction='row' justifyContent='flex-end'>
							<AnimateButton>
								<Button
									color='primary'
									variant='contained'
									type='submit'
									disabled={isSubmitting}
								>
									{buttonLabel}
								</Button>
							</AnimateButton>
						</Stack>
					</Grid>
				</SubCard>
			</Grid>
		</Grid>
	);
};

export default Profile;
